import { useState, ChangeEvent, FormEvent, FC, useEffect } from "react";
import { Form, Input, Button, Spacer } from "@heroui/react";
import { FormErrors, FormValues } from "@/types/Forms";
import { User } from "@/types/User";
import useAxios from "@/hooks/useAxios";
import axiosInstance from "@/services";
import { toast } from "react-hot-toast";
import { useTranslation } from "react-i18next";

const Profile = () => {
  const { data: user } = useAxios<User>({ url: "users/me", method: "get" });
  const [loading, setLoading] = useState(false);
  const i18n = useTranslation()
  const [errors, setErrors] = useState<FormErrors>({});
  const [values, setValues] = useState<FormValues>({
    name: "",
    whatsapp: "",
    country: "",
    state: "",
    city: "",
  });

  useEffect(() => {
    setUser();
  }, [user]);

  const setUser = () => {
    if (user) {
      setValues({
        name: user.name || "",
        whatsapp: user?.whatsapp || "",
        country: user?.country || "",
        state: user?.state || "",
        city: user?.city || "",
      });
    }
  };

  const validate = (): boolean => {
    const newErrors: FormErrors = {};
    if (!values.name) newErrors.name = i18n.t('errors.name_is_required');

    if (!values.whatsapp)
      newErrors.whatsapp = i18n.t('errors.whatsapp_number_required');
    if (!values.country) newErrors.country = i18n.t('errors.country_required');
    if (!values.state) newErrors.state = i18n.t('errors.state_required');
    if (!values.city) newErrors.city = i18n.t('errors.city_required');

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setValues((prev) => ({ ...prev, [name]: value }));
    setErrors((prev) => ({ ...prev, [name]: "" })); // Limpiar error al escribir
  };

  const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
    setLoading(true);
    e.preventDefault();
    if (validate()) {
      try {
        await axiosInstance.post("users/update", {
          ...values,
        });
        toast.success(i18n.t('errors.profile_updated_successfully'));
        setLoading(false);
      } catch (error) {
        console.error("fallo al actualizar al usuario", error);
        toast.error(i18n.t('errors.profile_update_failed'));
        setLoading(false);
      }
    }
  };
  if (!user?.name) return i18n.t('profile_view.loading');
  return (
    <div className="border border-1 p-4 relative rounded-lg border-[#3f3f3f]">
      <Form onSubmit={handleSubmit}>
        <span>{i18n.t('profile_view.personal_data')}</span>
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-4 w-full">
          <Input
            className=""
            label={i18n.t('profile_view.name')}
            name="name"
            value={values.name}
            onChange={handleChange}
            errorMessage={errors.name}
            isInvalid={!!errors.name}
            variant="bordered"
          />

          <Input
            className=""
            type="email"
            label={i18n.t('profile_view.email')}
            name="email"
            value={user?.email || ""}
            variant="bordered"
            disabled
          />
        </div>
        <Spacer y={1} />
        <span>Redes Sociales</span>
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-4 w-full">
          <Input
            className=""
            type="number"
            label="WhatsApp"
            name="whatsapp"
            value={values.whatsapp}
            onChange={handleChange}
            errorMessage={errors.whatsapp}
            isInvalid={!!errors.whatsapp}
            variant="bordered"
          />
        </div>
        <Spacer y={1} />
        <span>Direccion</span>
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-4 w-full">
          <Input
            className=""
            label={i18n.t('profile_view.country')}
            name="country"
            value={values.country}
            onChange={handleChange}
            errorMessage={errors.country}
            isInvalid={!!errors.country}
            variant="bordered"
          />

          <Input
            className=""
            label={i18n.t('profile_view.state')}
            name="state"
            value={values.state}
            onChange={handleChange}
            errorMessage={errors.state}
            isInvalid={!!errors.state}
            variant="bordered"
          />

          <Input
            className=""
            label={i18n.t('profile_view.city')}
            name="city"
            value={values.city}
            onChange={handleChange}
            errorMessage={errors.city}
            isInvalid={!!errors.city}
            variant="bordered"
          />
        </div>
        <Spacer y={1} />
        <Button type="submit" color="primary" size="lg">
          {i18n.t('profile_view.save')}
        </Button>
      </Form>
    </div>
  );
};

export default Profile;
